/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #484f59;
	font-family: "Alegreya Sans", sans-serif;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.5;
}

@media screen and (min-width: 600px) {
	body,
	button,
	input,
	select,
	optgroup,
	textarea {
		font-size: 22.4px;
		font-size: 1.4rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: "Alegreya", Georgia, Times, serif;
	line-height: 1.2;
	font-weight: 700;
}

h1 {
	font-size: 2em;
}

h2 {
	font-size: 1.8em;
}

h3 {
	font-size: 1.6em;
}

h4 {
	font-size: 1.4em;
}

h5 {
	font-size: 1.2em;
}

p {
	margin-top: .5em;
	margin-bottom: 1em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	position: relative;
	margin: 0 0 1em;
	background: #eee;
	padding: 1em 2em .5em 5em;
	font-style: italic;
}

blockquote:before {
	position: absolute;
	top: -.2em;
	left: -.13em;
	content: "“";
	font-family: "Alegreya Sans", sans-serif;
	font-size: 12em;
	line-height: 100%;
	color: white;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #f4f6f8;
	/* Fallback for when there is no custom background color defined. */
	margin-bottom: 0;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 1em 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	font-size: 14.4px;
	font-size: 0.9rem;
	line-height: 1;
	padding: .6em 1em .4em;
	cursor: pointer;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #ccc #bbb #aaa;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #2b3990;
	overflow-wrap: break-word;
	word-wrap: break-word;
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	-ms-hyphens: auto;
	-moz-hyphens: auto;
	-webkit-hyphens: auto;
	hyphens: auto;
}

a:visited {
	color: #8d87aa;
}

a:hover, a:focus, a:active {
	color: #ef4136;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

.posts-navigation,
.pagination {
	width: 100%;
	font-size: .8em;
	text-align: center;
}

.nav-links {
	font-size: 17.6px;
	font-size: 1.1rem;
}

.nav-links a,
.nav-links span.current {
	background: #f4f6f8;
	padding: 1em;
	display: inline-block;
	text-decoration: none;
}

.nav-links a:hover,
.nav-links span.current:hover {
	background: #ef4136;
	color: white;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
}

.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
	left: 100%;
}

.main-navigation ul ul a {
	width: 200px;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}

.main-navigation li {
	float: left;
	position: relative;
}

.main-navigation a {
	display: block;
	text-decoration: none;
}

.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
	color: white;
	background: #ef4136;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 960px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	float: left;
	width: 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

.menu-toggle {
	position: absolute;
	right: 10px;
	top: 10px;
	width: 30px;
	height: 30px;
	text-indent: -100vw;
	overflow: hidden;
	text-align: left;
	background: white;
	border: 0;
	background: url("img/flag.svg") no-repeat center center;
}

.main-navigation {
	background: #1b75bc;
}

.main-navigation ul {
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation li {
	width: 100%;
	margin: 0;
}

.main-navigation a {
	display: block;
	width: 100%;
	padding: 1em;
	text-decoration: none;
	background: #1b75bc;
	color: white;
	border: 1px solid #f4f6f8;
	transition: background-color .5s;
}

.main-navigation a:hover, .main-navigation a:focus {
	color: white;
	background: #ef4136;
}

@media screen and (min-width: 960px) {
	.main-navigation {
		background: none;
		font-size: 14px;
		letter-spacing: .2px;
	}
	.main-navigation ul {
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
		margin-right: 1em;
	}
	.main-navigation li {
		width: auto;
		margin: .25em;
	}
	.main-navigation a {
		padding: .5em 1em;
		border-radius: 4px;
	}
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
	/* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	font-size: .8em;
	background: #f4f6f8;
	padding: 1em 2em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

.widget h2.widget-title {
	font-size: 1.4em;
}

.widget ul,
.widget ol {
	padding-left: 0;
	margin-left: 0;
	list-style: none;
}

.widget ul li,
.widget ol li {
	margin-bottom: 0.5em;
	border-bottom: #8d87aa;
}

.widget ul ul,
.widget ul ol,
.widget ol ul,
.widget ol ol {
	margin-left: .5em;
}

@media screen and (min-width: 30em) {
	.widget ul ul,
	.widget ul ol,
	.widget ol ul,
	.widget ol ol {
		margin-left: 1em;
	}
}

/* Search widget */
.widget_search .search-submit {
	display: none;
}

.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;
}

.widget_calendar caption {
	padding: .5em 0;
	font-size: 1em;
	font-weight: 500;
	text-align: left;
}

.widget_calendar thead {
	background: transparent;
	color: black;
}

.widget_calendar thead th {
	border-bottom-width: 2px;
}

.widget_calendar td {
	padding: .2em;
	font-size: .8em;
	text-align: center;
	background: white;
	border: 1px solid #cccccc;
}

.widget_calendar th {
	border: none;
	text-align: center;
}

.widget_calendar .pad {
	background: #eaeaea;
}

.widget_calendar a {
	font-weight: 900;
	text-decoration: none;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Global layout
--------------------------------------------------------------*/
.site {
	width: 100%;
	max-width: 1280px;
	margin: 0 auto;
	background: #fff;
}

.site-content {
	padding: 1em;
	width: 100%;
}

@media screen and (min-width: 600px) {
	.site-content {
		padding: 2em 1em;
	}
}

@media screen and (min-width: 960px) {
	.site-content {
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
	}
	.site-content .content-area {
		width: 64%;
	}
	.site-content .widget-area {
		width: 30%;
	}
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.site-header {
	width: 100%;
	height: auto;
	background: #fff;
	border-bottom: 1px solid #f4f6f8;
	position: relative;
}

@media screen and (min-width: 960px) {
	.site-header {
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
	}
}

.site-header a {
	text-decoration: none;
}

.site-header .site-branding {
	font-family: "Alegreya", Georgia, Times, serif;
	margin: 1em;
	line-height: 1.1;
	min-width: 340px;
	display: flex;
	flex-wrap: nowrap;
	align-items: flex-start;
}

@media screen and (min-width: 600px) {
	.site-header .site-branding {
		width: 30%;
		flex-shrink: 0;
		margin: 1em 2em 1em;
	}
}

.site-header .site-branding .custom-logo-link {
	width: 60px;
	margin-right: 1rem;
}

@media screen and (min-width: 960px) {
	.site-header .site-branding .custom-logo-link {
		width: 80px;
	}
}

.site-header .site-branding .site-branding__text {
	overflow-x: visible;
	white-space: nowrap;
}

.site-header .site-branding .site-title,
.site-header .site-branding .site-description {
	margin: 0;
	padding: 0;
	font-weight: 700;
}

.site-header .site-branding .site-title {
	font-size: 1.4rem;
	line-height: 1.2;
}

@media screen and (min-width: 960px) {
	.site-header .site-branding .site-title {
		font-size: 1.8rem;
	}
}

.site-header .site-branding .site-title a {
	color: #484f59;
}

.site-header .site-branding .site-description {
	font-size: .8rem;
}

@media screen and (min-width: 960px) {
	.site-header .site-branding .site-description {
		font-size: 1.1rem;
	}
}

/*--------------------------------------------------------------
## Archive pages
--------------------------------------------------------------*/
@media screen and (min-width: 600px) {
	.archive-view .site-main {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
	}
}

.archive-view .archive-header {
	width: 100%;
	clear: both;
	display: block;
	flex-shrink: 0;
	border-top: 3px solid #484f59;
	margin-bottom: 1em;
	padding-top: .2em;
}

.archive-view .archive-header .page-title {
	margin-top: 0;
	font-size: .9em;
}

.archive-view .post, .archive-view .page {
	margin: 0 0 1.5em;
	box-shadow: 0 0 0 2px #f4f6f8;
	position: relative;
	border-bottom: 3px solid #ef4136;
}

@media screen and (min-width: 600px) {
	.archive-view .post, .archive-view .page {
		width: 48%;
	}
}

.archive-view .post figure.index-image, .archive-view .page figure.index-image {
	width: 100%;
	height: auto;
	max-height: 200px;
	overflow: hidden;
	margin: 0;
	border-bottom: 1px solid #f4f6f8;
}

.archive-view .post figure.index-image img, .archive-view .page figure.index-image img {
	width: 100%;
	object-fit: cover;
	object-position: center;
	transition: .5s all;
}

.archive-view .post figure.index-image img:hover, .archive-view .page figure.index-image img:hover {
	-webkit-filter: contrast(120%);
	/* Safari */
	filter: contrast(120%);
	transform: scale(1.1);
}

.archive-view .post .post_content, .archive-view .page .post_content {
	padding: 1em 1em 4em;
}

.archive-view .post .post_content .entry-header h2.entry-title, .archive-view .page .post_content .entry-header h2.entry-title {
	margin: 0 0 .5em;
	font-size: 1.3em;
	line-height: 1.2;
}

@media screen and (min-width: 600px) {
	.archive-view .post .post_content .entry-header h2.entry-title, .archive-view .page .post_content .entry-header h2.entry-title {
		font-size: 1em;
	}
}

.archive-view .post .post_content .entry-header h2.entry-title a, .archive-view .page .post_content .entry-header h2.entry-title a {
	text-decoration: none;
}

.archive-view .post .post_content .entry-header .entry-meta, .archive-view .page .post_content .entry-header .entry-meta {
	font-size: .8em;
	color: #aaa;
}

@media screen and (min-width: 600px) {
	.archive-view .post .post_content .entry-header .entry-meta, .archive-view .page .post_content .entry-header .entry-meta {
		font-size: .6em;
	}
}

.archive-view .post .post_content .entry-header .entry-meta a, .archive-view .page .post_content .entry-header .entry-meta a {
	color: #aaa;
}

.archive-view .post .post_content .entry-content, .archive-view .page .post_content .entry-content {
	margin: 0;
	overflow-wrap: break-word;
	word-wrap: break-word;
	-ms-word-break: break-all;
	word-break: break-all;
	word-break: break-word;
	-ms-hyphens: auto;
	-moz-hyphens: auto;
	-webkit-hyphens: auto;
	hyphens: auto;
}

@media screen and (min-width: 600px) {
	.archive-view .post .post_content .entry-content, .archive-view .page .post_content .entry-content {
		font-size: .8em;
	}
}

.archive-view .post .post_content .entry-footer, .archive-view .page .post_content .entry-footer {
	font-size: .8em;
}

.archive-view .post .post_content .entry-footer > span:after,
.archive-view .post .post_content .entry-meta > span:after, .archive-view .page .post_content .entry-footer > span:after,
.archive-view .page .post_content .entry-meta > span:after {
	content: "|";
	margin: 0 .2em;
}

.archive-view .post .post_content .entry-footer > span:last-child:after,
.archive-view .post .post_content .entry-meta > span:last-child:after, .archive-view .page .post_content .entry-footer > span:last-child:after,
.archive-view .page .post_content .entry-meta > span:last-child:after {
	content: "";
	margin: 0;
}

.archive-view .post .post_content .continue-reading a, .archive-view .page .post_content .continue-reading a {
	position: absolute;
	bottom: 0;
	right: 0;
	background: #f4f6f8;
	padding: 1em;
	text-decoration: none;
	font-size: .8em;
}

.archive-view .post .post_content .continue-reading a:hover, .archive-view .page .post_content .continue-reading a:hover {
	background: #ef4136;
	color: white !important;
}

/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.updated:not(.published) {
	display: none;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.single .page-content,
.single .entry-content,
.single .entry-summary {
	margin: 1.5em 0 0;
}

.single .entry-meta {
	color: #2b3990;
}

.single .entry-meta a {
	color: #2b3990;
}

.single .entry-meta > span:after {
	content: "|";
	margin: 0 .4em;
}

.single .entry-meta > span:last-child:after {
	content: "";
	margin: 0;
}

.single .entry-meta span.posted-on:before {
	content: "\f073";
	font-family: 'FontAwesome';
	margin-right: .4em;
}

.single .entry-meta span.byline:before {
	content: "\f007";
	font-family: 'FontAwesome';
	margin-right: .2em;
}

.single .entry-meta span.cat-links:before {
	content: "\f02b";
	font-family: 'FontAwesome';
	margin-right: .2em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comments-area {
	border-top: 1px solid #909acd;
}

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## Header
--------------------------------------------------------------*/
.comments-area {
	border-top: 1px solid #909acd;
}

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
## Footer
--------------------------------------------------------------*/
.site-footer {
	background: #2b3990;
	color: #f4f6f8;
	padding: 2em 1em;
	font-size: .9em;
}

@media screen and (min-width: 960px) {
	.site-footer {
		padding: 2em;
	}
}

.site-footer a {
	color: white;
}

.site-footer .footer__wrap {
	text-align: center;
}

@media screen and (min-width: 600px) {
	.site-footer .footer__wrap {
		text-align: left;
		display: flex;
		flex-wrap: nowrap;
	}
	.site-footer .footer__wrap .social-menu {
		flex: 1;
	}
	.site-footer .footer__wrap .site-info {
		flex: 1;
		text-align: right;
	}
}

.site-footer .footer__wrap .social-menu ul {
	list-style: none;
	margin-left: 0;
	padding-left: 0;
}

.site-footer .footer__wrap .social-menu ul li {
	display: inline;
	margin: 0 .5em;
}

.site-footer .footer__wrap .site-info {
	font-size: .8em;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
## SVG Icons
--------------------------------------------------------------*/
/* SVG Icons base styles */
.icon {
	display: inline-block;
	fill: currentColor;
	height: 1em;
	position: relative;
	/* Align more nicely with capital letters */
	top: -0.0625em;
	vertical-align: middle;
	width: 1em;
}

/*--------------------------------------------------------------
SVG Fallbacks
--------------------------------------------------------------*/
.svg-fallback {
	display: none;
}

.no-svg .svg-fallback {
	display: inline-block;
}

.no-svg .nav-links .svg-fallback {
	padding: 0 .5em;
	color: inherit;
	background: inherit;
}

.no-svg .svg-fallback.icon-arrow-long-left:before {
	content: "←";
}

.no-svg .svg-fallback.icon-arrow-long-right:before {
	content: "→";
}

/* Social Menu fallbacks */
.no-svg .social-menu a {
	height: auto;
	width: auto;
}

.no-svg .social-menu li a .screen-reader-text {
	clip: auto;
	font-size: 16px;
	font-size: 1rem;
	font-weight: 400;
	height: auto;
	position: relative !important;
	/* overrides previous !important styles */
	width: auto;
}
